import React, {useState, useEffect, useReducer} from "react";
import axios from "axios";
import Buttoni from "../Buttoni/Buttoni";
import Smska from "../Smska/Smska";
import Buttoniany from "../Buttoniany/Buttoniany";
import Export from "../Export/Export";
import TradeCurrency from "../TradeCurrency/TradeCurrency";
import './Dialog.css'
import Deposit from "../Deposit/Deposit";
import Deposit2 from "../Deposit/Nakopschet";
import EqualButtons from "../EqualButtons";

export default function Dialog({yourmessage}) {

    const [valutate, setValutate] = useState('');
    const [money, setMoney] = useState(false);
    const [counts, setCounts] = useState(false);
    const [message, setMessage] = useState('');
    const [banker, setBank] = useState('');
    const [messages, setMessages] = useState([]);
    const [input, setInput] = useState("");
    const [country, setCountry] = useState('');
    const handleClearMessages = () => {
        setMessages([
            {
                sender: 'bot-message', component: (
                    <Buttoniany
                        first=" ,    ,  "
                        buttons={[' ', '  ', '   ']}
                        onClickHandler={[comissia, trade_valuta, vkladiandscheta]}
                    />
                )
            }
        ]);
    }
    const point = {
        Balance: 0,
        Plus: 0,
        Money: 0,
        Kopit: 0,
        Big: 0,
        Vozmozh: 0,
    }
    const pointnames = {
        Balance: ' ',
        Plus: ' ',
        Money: ' ',
        Kopit: '',
        Big: ' ',
        Vozmozh: ' ',
    }

    const comissia = () => {
        console.log("comissia")
        setMessages(prev => [
            ...prev,
            {sender: 'user-message', text: ' '},
            {
                sender: 'bot-message',
                component: (
                    <>
                        <p>  </p>
                        <Export func={countries} choose={2}/>
                    </>
                ),
            },
        ]);
    };

    useEffect(() => {
        setMessages([
            {sender: 'user-message', text: yourmessage},
            {
                sender: 'bot-message',
                component: (
                    <Buttoniany
                        first=" ,     ?"
                        buttons={[' ', '  ', '   ']}
                        onClickHandler={[comissia, trade_valuta, vkladiandscheta]}
                    />
                ),
            },
        ]);
    }, []);
    const countries = (valu) => {
        setValutate(valu);
        setMessages(prev => [
            ...prev,
            {sender: 'user-message', text: valu},
            {
                sender: 'bot-message', component: (
                    <>
                        <p>       </p>
                        <Export func={valuta} choose={1}/>
                    </>
                )
            }
        ]);
    }
    const vkladiandscheta = (info) => {
        setMessages(prev => [...prev,
            {sender: 'user-message', text: info},
            {
                sender: 'bot-message', component: (
                    <Buttoniany
                        first={'    '}
                        buttons={['  ', ' ']}
                        onClickHandler={[nakopschet, vklad]}
                    />
                )
            }
        ])
    };
    const nakopschet = (text) => {
        setMessages(prev => [...prev,
            {sender: 'user-message', text: text},
            {
                sender: 'bot-message', component: (
                    <>
                        <div className="promo-banner">
                            <h2>   ?</h2>
                            <p>
                                           
                                 .    ,  
                                    . 
                            </p>
                        </div>

                        <Buttoniany
                            first={''}
                            buttons={[' ', '  ', '   ', ' ']}
                            onClickHandler={[nakopschetinfo, nakopshetchoose, nakopshetcategor, vkladiandscheta]}
                        />
                    </>

                )
            }
        ])
    }
    const nakopshetcategor = (info) => {
        setMessages(prev => [...prev,
            {sender: 'user-message', text: info},
            {
                sender: 'bot-message', component: (
                    <>
                        <p>           </p>
                        <a href="https://www.gazprombank.ru/personal/increase/deposits/">https://www.gazprombank.ru/personal/increase/deposits/</a>
                    </>
                )
            }

        ])
    }
    const nakopshetchoose = (info) => {
        setMessages(prev => [...prev,
            {sender: 'user-message', text: info},
            {
                sender: 'bot-message', component: (
                    <Buttoni first={'       ?'}
                             buttons={['  10 ', '  10 ']}
                             onClickHandler={nakopshetchoosestep2}
                    />
                )
            }
        ])
    }
    const nakopshetchoosestep2 = (choose) => {
        if (choose === '  10 ') {
            setMoney(true)
        } else {
            setMoney(false)
        }

        setMessages(prev => [...prev,
            {sender: 'user-message', text: choose},
            {
                sender: 'bot-message', component: (
                    <Buttoni
                        first={'        ?'}
                        buttons={[' ', ' ']}
                        onClickHandler={nakopshetchoosestep3}
                    />
                )
            }


        ])
    }
    const nakopshetchoosestep3 = (info) => {
        if (info === ' ') {
            setCounts(true)
        } else {
            setCounts(false)
        }
        setMessages(prev => [...prev,
            {sender: 'user-message', text: info},
            {
                sender: 'bot-message', component: (
                    <Buttoni first={'   ?'}
                             buttons={['  ( 21,5 % )', '  (19,5 %   )']}
                             onClickHandler={nakopshetchoosestep4}
                    />
                )
            }

        ])

    }
    const nakopshetchoosestep4 = (info) => {
        const sms = info === '  ( 21,5 % )' ? '' : ' '
        setMessages(prev => [...prev,
            {sender: 'user-message', text: sms},
        ])
        if (money && !counts && info === '  ( 21,5 % )') {
            setMessages(prev => [...prev,
                {sender: 'bot-message', text: 'Premium'},])
        }
        if (!money && !counts && info === '  ( 21,5 % )') {
            setMessages(prev => [...prev,
                {sender: 'bot-message', text: ' '},])
        }
        if (counts && info === '  ( 21,5 % )') {
            setMessages(prev => [...prev,
                {sender: 'bot-message', text: ' '},])
        }
        if (info === '  (19,5 %   )') {
            setMessages(prev => [...prev,
                {sender: 'bot-message', text: ' '},])
        }
        setMessages(prev => [...prev,
            {
                sender: 'bot-message', component: (
                    <Buttoniany
                        first="      - ?"
                        buttons={[' ', '  ', '   ']}
                        onClickHandler={[comissia, trade_valuta, vkladiandscheta]}
                    />
                )
            }

        ])


    }
    const nakopschetinfo = (info) => {
        setMessages(prev => [...prev,
            {sender: 'user-message', text: info},
            {
                sender: 'bot-message', component: (
                    <Deposit2/>
                )
            }
        ])
    }
    const vklad = (text) => {
        setMessages(prev => [...prev,
            {sender: 'user-message', text: text},
            {
                sender: 'bot-message', component: (
                    <>
                        <div className="promo-banner">
                            <h2>    ?</h2>
                            <p>
                                <strong></strong>    <strong>   
                                </strong>,
                                  <strong></strong>    <strong> 
                                </strong>.<br/>
                                 <strong>   </strong>  <strong> 
                                  </strong>    ! 
                            </p>
                        </div>
                        <Buttoniany
                            first={''}
                            buttons={['   ', ' ', ' ']}
                            onClickHandler={[helpchoosevklad, categories, privileges]}
                        />
                    </>

                )
            }
        ])
    }
    const helpchoosevklad = (text) => {
        setMessages(prev => [...prev,
            {sender: 'user-message', text: text},
            {
                sender: 'bot-message', component: (
                    <EqualButtons
                        first={'       '}
                        buttons={['', '']}
                        onClickHandler={currency}
                        value={['', '']}
                    />

                )
            }
        ])
    }
    const currency = (voted) => {
        setMessages(prev => [...prev,
            {sender: 'user-message', text: voted},
        ])
        if (voted === '') {
            point.Balance += 1
            point.Plus += 1
            point.Money += 1
            point.Kopit += 1
            point.Big += 1

        } else {
            point.Vozmozh += 1
        }
        setMessages(prev => [...prev,
            {
                sender: 'bot-message', component: (
                    <Buttoni
                        first={'       ?'}
                        buttons={['1 - 3 ', '4 - 7 ', '8  - 3 ']}
                        onClickHandler={times}
                    />
                )
            },

        ])


    }
    const times = (times) => {
        setMessages(prev => [...prev,
            {sender: 'user-message', text: times},
        ])
        if (times === '1 - 3 ' || times === '4 - 7 ') {
            point.Balance += 1
            point.Money += 1
            point.Kopit += 1
            point.Plus += 1
        }
        setMessages(prev => [...prev,
            {
                sender: 'bot-message', component: (
                    <Buttoni
                        first={'     ?'}
                        buttons={['   / ', '  ']}
                        onClickHandler={procents}
                    />
                )
            }
        ])
    }
    const procents = (procents) => {
        setMessages(prev => [...prev,
            {sender: 'user-message', text: procents}
        ])
        if (procents === '  /') {
            point.Balance += 1
        } else {
            point.Plus += 1
            point.Money += 1
            point.Kopit += 1
            point.Big += 1
        }
        setMessages(prev => [...prev,
            {
                sender: 'bot-message', component: (
                    <>
                        <div className="promo-banner">
                            <h2>     - ,     
                                ?</h2>
                            <p>
                                1) ,     <br/>
                                2)      <br/>
                                3)       <br/>
                                4)   (  30    ) <br/>
                                5)    // ! 
                            </p>
                        </div>
                        <Buttoni first={''}
                                 buttons={['1', '2', '3', '4', '5']}
                                 onClickHandler={ysl}
                        />
                    </>


                )
            }
        ])
    }
    const ysl = (vote) => {
        setMessages(prev => [...prev,
            {sender: 'user-message', text: vote}
        ]);

        if (vote === '1') {
            point.Big += 1;
        } else if (vote === '2') {
            point.Balance += 1;
        } else if (vote === '3') {
            point.Plus += 1;
        } else if (vote === '4') {
            point.Money += 1;
        } else {
            point.Kopit += 1;
        }

        const maxValue = Math.max(...Object.values(point));

        const itog = [];
        for (const key in point) {
            if (point[key] === maxValue) {
                itog.push(key);
            }
        }

        const user_itog = itog.map(key => pointnames[key]);

        console.log(' :', user_itog);
        setMessages(prev => [...prev,
            {
                sender: 'bot-message', component: (
                    <>

                        {user_itog.map((key, itogi) => (
                            <p>    : {key}</p>
                        ))
                        }
                    </>
                )
            },


        ])
    };

    const categories = (text) => {
        setMessages(prev => [...prev,
            {sender: 'user-message', text: text},
            {
                sender: 'bot-message', component: (
                    <>
                        <p>           </p>
                        <a href="https://www.gazprombank.ru/personal/increase/deposits/">https://www.gazprombank.ru/personal/increase/deposits/</a>
                    </>

                )
            }
        ])
    }
    const privileges = (text) => {
        setMessages(prev => {
            return [
                ...prev,
                {sender: 'user-message', text},
                {
                    sender: 'bot-message',
                    component: (
                        <>
                            <Deposit/>
                        </>
                    )
                }
            ];
        });
    };


    const trade_valuta = () => {
        setMessages(prev => [
            ...prev,
            {sender: 'user-message', text: ' '},
            {
                sender: 'change-moneta', component: (<TradeCurrency/>)
            },
            {
                sender: 'second-bot-message', component: (
                    <Buttoniany
                        first="      - ?"
                        buttons={[' ', '  ', '   ']}
                        onClickHandler={[comissia, trade_valuta, vkladiandscheta]}
                    />
                )
            }
        ]);
    };

    const valuta = (country) => {
        setCountry(country)
        setMessages(prev => [
            ...prev,
            {sender: 'user-message', text: country},
            {sender: 'bot-message', text: '      '},
        ]);
    };

    const Sendtoback = async (data) => {
        try {
            const response = await axios.post("http://127.0.0.1:8000/", data, {
                    headers: {"Content-Type": "application/json"},
                })
            ;
            setMessages(prev => [...prev, {
                sender: 'bot-message', component: (
                    <>
                        <p>  : undefined </p>
                    </>
                )
            }])
            console.log("    ", response.data);
        } catch (error) {
            console.error("   :", error);
        }
    };

    const bank = (bankik, summa) => {
        setBank(bankik);
        setMessages(prev => [
            ...prev,
            {sender: 'user-message', text: bankik},
            {
                sender: 'bot-message',
                component: (
                    <>
                        <h3>   </h3>
                        <p> : {valutate}</p>
                        <p> : {country}</p>
                        <p> : {summa}</p>
                        <p>  : {bankik}</p>
                        <button className="button" onClick={() => Sendtoback({valutate, summa, bankik, country})}>
                             
                        </button>
                    </>

                ),
            },
        ]);
    };
    const bankers = (msg) => {
        setMessages(prev => [
            ...prev,
            {
                sender: 'bot-message',
                component: (
                    <>
                        <p>      </p>
                        <Export func={bank} choose={0} val2={msg}/>
                    </>
                ),
            },
        ]);
    }
    const handleSend = (e) => {
        e.preventDefault();
        const trimmed = input.trim();
        if (!trimmed) return;
        setMessages(prev => [...prev, {sender: 'user-message', text: trimmed}]);
        if (valutate && !message) {
            const msg = trimmed;
            if (!isNaN(msg) && isFinite(msg) && Number(msg) > 0) {
                if (valutate && !message) {
                    setMessage(msg);
                    bankers(msg)
                }
            } else {
                setMessages(prev => [
                    ...prev,
                    {sender: 'bot-message', text: '   '},
                ]);
            }
        } else {
            setMessages(prev => {
                const lastComponent = [...prev].reverse().find(m => m.component)?.component;
                return [
                    ...prev,
                    {sender: 'bot-message', text: ' ,      '},
                    lastComponent && {sender: 'second-bot-message', component: lastComponent}
                ].filter(Boolean);
            });
        }
        setInput("");
    };
    return (
        <div>
            <Smska
                input={input}
                handleSend={handleSend}
                setInput={setInput}
                messages={messages}
                clear={handleClearMessages}
            />
        </div>
    );
}
